import { FC, useState } from 'react'
import { MultiDateTimeSelect, TimeSelectOption } from '..'
import { parse, format, addHours, differenceInMinutes } from 'date-fns'

const STEP_TIME = 30
const TIME_FORMAT = 'hh:mm a'
const SELECTED_TIME = '11:00 AM'
const SELECTED_DATE = new Date(new Date().setHours(11, 0))
const getTimeInString = (time: number): string => (time > 9) ? `${time}` : `0${time}`

export const getEndTimeOpening = (startDate: Date): string => {
  const hours = getTimeInString(startDate.getHours() + 1)
  const minutes = getTimeInString(startDate.getMinutes())

  return `${hours}:${minutes} ${format(startDate, 'aaa')}`
}

const countHours = (value: Date): string => (differenceInMinutes(value, SELECTED_DATE) / 60).toFixed(1)

export const TimeSelectExample: FC = () => {
  const parsedTime = parse(SELECTED_TIME, TIME_FORMAT, SELECTED_DATE)
  const START_TIME = format(addHours(parsedTime, 1), TIME_FORMAT)
  const parsedStartT = parse(START_TIME, TIME_FORMAT, SELECTED_DATE)
  const [timeValue, setTimeValue] = useState<TimeSelectOption>({
    time: START_TIME,
    date: parsedStartT
  })

  const renderOption = (option: TimeSelectOption): React.ReactNode => {
    const value = option.date
    const placeholder = value.getDate() === SELECTED_DATE.getDate() ? `(${countHours(value)}h)` : `(${countHours(value)}h - next day)`

    return (
      <>
        {option.time ?? ''}
        <span style={{ color: '#212121', opacity: 0.5, marginLeft: '6px' }}>{placeholder}</span>
      </>
    )
  }

  return (
    <MultiDateTimeSelect
      value={timeValue}
      startDate={SELECTED_DATE}
      onChange={setTimeValue}
      opening={START_TIME}
      closing={SELECTED_TIME}
      step={STEP_TIME}
      renderOption={renderOption}
      isNextDay
    />
  )
}

export { MultiDateTimeSelect } from '..'
