import { parse, format, addMinutes, differenceInMinutes, isValid, addDays, addHours } from 'date-fns'
import { TimeSelectOption } from '..'

const MINUTES_IN_HOUR = 60
const DEFAULT_STEP = 15
const TIME_FORMAT = 'hh:mm a'
const DEFAULT_START = '05:00 AM'
const DEFAULT_END = '11:45 PM'
const INTERVAL_REGEX = /^([\d]{2}):([\d]{2}) (AM|am|PM|pm)$/

export const createMultiDateTimeIntervals = (
  step: number,
  startTime?: string | null,
  endTime?: string | null,
  isNextDay?: boolean,
  startDate?: Date
): TimeSelectOption[] => {
  const start = (startTime != null && INTERVAL_REGEX.test(startTime))
    ? startTime
    : DEFAULT_START

  const end = (endTime != null && INTERVAL_REGEX.test(endTime))
    ? endTime
    : DEFAULT_END

  // step should be greater than 0 and be a divisor of an hour for correct math
  const isStepADivisor = step > 0 && Math.floor(MINUTES_IN_HOUR / step) === MINUTES_IN_HOUR / step
  const _step = Math.min(isStepADivisor ? step : DEFAULT_STEP, MINUTES_IN_HOUR)

  const date = startDate ?? new Date(0)
  const nextDay = isNextDay != null ? addDays(date, 1) : date

  const startPeriod = date.getDate() === addHours(date, 1).getDate() ? date : addDays(date, 1)
  // If selected time is equal or more than 23.00 than start of period will be the next day

  const startT = parse(start, TIME_FORMAT, startPeriod)
  const endT = parse(end, TIME_FORMAT, nextDay)

  if (!isValid(startT) || !isValid(endT) || endT.getTime() < startT.getTime()) {
    return []
  }

  const res: TimeSelectOption[] = []
  const diff = differenceInMinutes(endT, startT)

  for (let i = 0; i <= diff; i += _step) {
    res.push(
      {
        time: format(
          addMinutes(startT, i),
          TIME_FORMAT),
        date: addMinutes(startT, i)
      })
  }

  return res
}
