import { ComponentType, FC, HTMLAttributes, useMemo } from 'react'
import { observer } from 'mobx-react-lite'

import { Dropdown } from '@UI/Form/Dropdown'
import { createMultiDateTimeIntervals } from './utils/createMultiDateTimeIntervals'

export interface MultiDateTimeSelectProps {
  onChange: (val: TimeSelectOption) => void
  value: TimeSelectOption
  label?: string
  touched?: boolean
  error?: string
  showError?: boolean
  disabled?: boolean
  step?: number // number of minutes, max = 60
  id?: string
  opening?: string | null
  closing?: string | null
  dataTestId?: string
  classes?: {
    root?: string
    option?: string
  }
  DropdownIconComponent?: ComponentType<HTMLAttributes<HTMLElement>> | null
  renderOption?: (value: TimeSelectOption) => string | React.ReactNode
  isNextDay?: boolean
  startDate?: Date
}

export interface TimeSelectOption {
  time: string
  date: Date
}

const defaultRenderOption = (value: TimeSelectOption): string => value.time

/**
 * Dropdown with the list of time slots. List of items
 * is described as a range of [opening:closing] values, where:
 *   opening - format: hh:mm AM|PM|am|pm
 *   closing - format: hh:mm AM|PM|am|pm
 */

export const MultiDateTimeSelect: FC<MultiDateTimeSelectProps> = (props) => {
  const {
    onChange,
    value,
    label,
    disabled,
    opening,
    closing,
    error,
    touched,
    showError = touched === true && (error !== '' && error != null),
    step = 15,
    id,
    dataTestId = id,
    classes,
    DropdownIconComponent,
    renderOption = defaultRenderOption,
    isNextDay = false,
    startDate
  } = props

  const timeIntervals = useMemo(() => (
    createMultiDateTimeIntervals(step, opening, closing, isNextDay, startDate)
  ), [step, opening, closing, isNextDay, startDate])

  return (
    <Dropdown
      id={id ?? 'time-select-v2'}
      label={label}
      value={value ?? {}}
      renderOption={renderOption}
      onChange={(_e: any, newValue: TimeSelectOption | null) => {
        if ((newValue != null)) {
          onChange(newValue)
        }
      }}
      classes={{
        wrapper: classes?.root,
        option: classes?.option
      }}
      disabled={disabled}
      options={timeIntervals}
      showError={showError}
      touched={touched}
      error={error}
      dataTestId={dataTestId}
      getOptionLabel={(option: TimeSelectOption) => option.time}
      disableClearable
      openOnFocus
      DropdownIconComponent={DropdownIconComponent}
    />
  )
}

export default observer(MultiDateTimeSelect)
